@import './variables.sass';

@media screen and (min-width: 500px) {
  .footer {
    padding: 20px;
    margin: 40px auto 20px; }
  button.normalBtn {
    padding: .5rem 1rem !important; }
  .header {
    margin: 0px 0 40px 0;
    display: flex;
    flex-direction: column; }
  .header > h1 {
    flex: 1;
    text-align: left;
    line-height: 1;
    text-align: center;
    margin: 20px 0 40px 0; }
  .logo {
    font-size: 1.5rem !important; }
  .menu {
    flex: none;
    height: auto;
    display: flex;
    justify-content: center;
    width: auto; }
  .navLink {
    margin-left: 20px;
    font-size: .9rem;
    padding: 1rem 0;
    &:last-child {
      border: none; } }

  .otherInfo {
    margin: 0; }

  .basicInfo {
    padding: 0; }

  .infoWrapper {
    flex-direction: row; }

  .item {
    flex: 0 0 calc(50% - 20px);
    &:nth-child(2n) {
      margin-left: 40px; } }


  .itemPageWrapper {
    flex-direction: row;
    .itemInfoWrapper {
      margin-left: 2rem;
      margin-top: 0; }
    .reqTradeBtn {
      margin: 3rem 0 2rem 0;
      border: solid 2px $black; } }
  .uIWrapper {
    margin: 0 0 4rem 0;
    .upper {
      flex-direction: row; }
    .itemCost {
      color: lighten($black, 20%);
      font-size: 1.2rem;
      margin: 1rem 0;
      font-weight: 500; }
    .addDate {
      margin: .5rem 0;
      font-weight: 500; }
    .itemDescription {
      margin: 2rem 0; }
    .itemInfo {
      margin-left: 3rem; }
    .userImg {
      margin-top: .2rem;
      flex: 0 0 10rem;
      height: 10rem;
      width: 10rem; } }
  .myItemsWrapper {
    padding: 0 0 2rem 0; } }

@media screen and (max-width: 700px) and (min-width: 500px) {
  .item {
    margin-bottom: 20px;
    flex: 0 0 calc(50% - 10px);
    &:nth-child(2n) {
      margin-left: 20px; } } }




